                }
            }

            .phone_win {
                width: 375px;
                height: 668px;
                background: #ffffff;
                position: absolute;
                left: 13px;
                top: 78px;
                overflow-y: scroll;

                .preview_content {
                    width: 100%;
                    min-height: 100%;
                    position: relative;
                }
            }
        }

        .handle_edit {
            width: 100%;
            padding: 10px;
